import React from 'react';
import {Link} from "react-router-dom";
import {motion} from 'framer-motion';
import {Button} from "@chakra-ui/button";
import {usePageUpdate} from "../contexts/PageContext";
import {
    FaBong,
    FaCode,
    FaFacebookMessenger,
    FaInfo,
    FaList, FaPhone, FaPhoneAlt,
    FaProjectDiagram,
    GiGingerbreadMan,
    GiPerson
} from "react-icons/all";

function HeaderButtonGroup(props) {
    const MotionButton = motion(Button);
    const changePage = usePageUpdate();

    return (
        <motion.div style={{marginLeft:"10px"}}
        exit={{opacity: 0, x: -100, rotateY: -35, transition: {ease: "easeOut", duration: 0.5}}}>

        <Link to="/projects">
            <MotionButton
                leftIcon={<FaCode/>}
                onClick={()=>changePage(2)}
                initial={{opacity: 0, y: -30, x: -15}}
                animate={{opacity: 1, y: 0}}
                transition={{delay: 0.5, duration: 0.5}}
                variant="outline" mt={8} colorScheme="blue"
            >Projects
            </MotionButton>
        </Link>
    <Link to="/about">
        <MotionButton
            leftIcon={<FaList />}
            initial={{opacity: 0, y: 30}}
            animate={{opacity: 1, y: 0}}
            transition={{delay: 0.5, duration: 0.5}}
            mt={8} variant="outline" colorScheme="blue">About</MotionButton>
    </Link>

    &emsp;
    <MotionButton
        leftIcon={<FaPhoneAlt/>}
        initial={{opacity: 0, y: -30}}
        animate={{opacity: 1, y: 0}}
        transition={{delay: 0.5, duration: 0.5}}
        mt={8} variant="outline" colorScheme="blue" >
        Contact
    </MotionButton>
        </motion.div>
    );
}

export default HeaderButtonGroup;