import React from 'react';
import {Button} from "@chakra-ui/button";
import {motion, useAnimation} from 'framer-motion'
import {usePage} from "../contexts/PageContext";
import {Link} from "react-router-dom";

function AnimatedButton({id, text, switchPage, hideMenu, path }) {
    const page = usePage();
    const MotionButton = motion(Button)
    const buttonControls = useAnimation();
    const buttonAnimation = {
        anim: {scale: 1.2},
        init: {scale:1.1}
    }
    return (
        <>

            <Link to={path}>
                <MotionButton
                    onHoverStart={() => buttonControls.start(buttonAnimation.anim)}
                    onHoverEnd={() => buttonControls.start(buttonAnimation.init)}
                    animate={buttonControls}

                    color={page === id ? "cyan.400" : "#ffffff"}
                    onClick={id === 1 ? () => hideMenu() : () => switchPage(id)} variant="ghost">{text}</MotionButton>
            </Link>
        </>
    );
}

export default AnimatedButton;