import React, {useEffect, useState, useContext} from 'react';
import AnimatedButton from "./AnimatedButton";
import {motion, useAnimation} from 'framer-motion'
import {usePage, usePageUpdate} from "../contexts/PageContext";
import { useLocation } from 'react-router-dom'

function Menu() {
        const pages = [
            {id: 1, name: "Home", path: "/"},
            {id: 2, name: "Projects", path: "/projects"},
            {id: 3, name: "About", path: "/about"},
            {id: 4, name: "Contact", path: "/contact"},
        ]
        const menuVariants = {
            hidden: {opacity: 0, y: -20, transition: {duration: 1}},
            visible: {opacity: 1, y: 0, transition: {duration: 1}}
        };
        const menuControl = useAnimation();

        const selectedPage = usePage();

        const selectPage = usePageUpdate();

        const {pathname} = useLocation();

        const hideMenu = () => {
            menuControl.start(menuVariants.hidden)
        }
        useEffect(() => {

            pathname !== "/" && menuControl.start(menuVariants.visible);
        },[selectedPage, menuControl,menuVariants])
        return (
            <motion.div
                variants={menuVariants}
                initial="hidden"
                animate={menuControl}
            >
                {pages.map(({id, name, path}) =>

                    <AnimatedButton
                        hideMenu={hideMenu}
                        id={id}
                        key={id}
                        selectedPage={selectedPage}
                        switchPage={selectPage}
                        text={name}
                        path={path}
                    />)}
            </motion.div>
        );
}

export default Menu;